import React, { useState } from 'react';
import { useSpring, animated } from '@react-spring/web';
const AnimatedRect = () => {

  const [styles, api] = useSpring(() => ({
    from: {x: 0, y: 0},
    to: {x: 0, y: 0},
    delay: 0,
    loop: {reverse: true},
    config: {
      frequency: 1,
      velocity: 100,
      tension: 170,
      friction: 1,
      duration: 100,
    }
  }))
  const [counter, setCounter] = useState(window.crypto.getRandomValues(new Uint16Array(10))[0] / 10000)
  const [counter1, setCounter1] = useState(window.crypto.getRandomValues(new Uint16Array(10))[0] / 10000)
  console.log(counter, counter1)
  console.log(String(counter).at(3))
  return (
    <animated.div style={{
      width: 200, ...styles}} onMouseEnter={() => {
        // console.log(counter)
        setCounter(window.crypto.getRandomValues(new Uint16Array(10))[0] / 10000 + 1)
        setCounter1(window.crypto.getRandomValues(new Uint16Array(10))[0] / 10000 + 1)
        api.start({to: {x: 100 * counter, y: 100 * counter1}})
       styles.to = {x: 100};}}>
      here we go
    </animated.div>
  );
};

export default AnimatedRect;