import React, { useState } from 'react';
import { useSpring, animated } from '@react-spring/web';
const AnimatedTimer = (props) => {

  const [flip, set] = useState(false);

  const  {number} = useSpring({
    reset: true,
    reverse: flip,

    from: 3, //??
    
    // to: {number: 3},
    config: {
      duration: 1000,
      frequency: 1,
      // decay: true,
      bounce: 0,
      precision: 0,
      damping: 1,
      // velocity: 0.1,
      tension: 0,
      mass: 1,

      friction: 0,
    },
    number: 1,
    // delay: 200,
    onRest: () => set(!flip),
  })

  function HandleClick() {
    console.log({number})
    // number.from = 2;
  }
  return (
    <>
      <animated.div>
        {number.to((n) => {
          {/* console.log(n); */}
          return n.toFixed(3)})}
        {/* {number} */}
      </animated.div>
      <button className='button' onClick={HandleClick}>Click to start timer</button>
      
    </>

  );
};

export default AnimatedTimer;