import React from 'react';
import { useSpring, a } from '@react-spring/web';
import { useDrag } from '@use-gesture/react';
import { useForceUpdate } from '@react-spring/shared';
const AnimatedSlide = () => {
  const ForceUpdated = useForceUpdate();
  const Drag = useDrag(({down, movement: [mx], cancel}) => {
    if (mx > 200 || mx < -200) {
      slider_api.stop()
      setTimeout(() => {slider_api.start({scale: 1, x: 0})}, 200);
      setTimeout(() => {leftSliderCircle_api.start({scale: 0})}, 200);
      setTimeout(() => {rightSliderCircle_api.start({scale: 0})}, 200);

      // ForceUpdated()
      cancel();

    };
    if (mx > 0 && down) {
      document.querySelector(".slider_left-side").classList.add("active");
      document.querySelector(".slider_right-side").classList.remove("active");
      leftSliderCircle_api.start({
        to: {
          scale: mx / 100,
  
        }
      })

    }
    if (mx < 0 && down) {
      document.querySelector(".slider_right-side").classList.add("active");
      document.querySelector(".slider_left-side").classList.remove("active");
      rightSliderCircle_api.start({
        to: {
          scale: mx / 100,
  
        }
      })
    }
 
    slider_api.start({
      scale: down ? 1.2 : 1,
      x: down ? mx : 0,
      // y: down ? my : 0,
    })
  })
  const [leftSliderCircle, leftSliderCircle_api] = useSpring(() => ({
    // width: 20,
    // height: 20,
    scale: 0,
    reverse: {scale: 0},

  }))
  const [rightSliderCircle, rightSliderCircle_api] = useSpring(() => ({
    // width: 200,
    // height: 20,
    scale: 0,
    reverse: true,
  }))
  const [sliderStyle, slider_api] = useSpring(() => ({
    scale: 1,
    x: 0,
    y: 0,
    
  }))

  return (
    <div className='slider'>
      <a.div className="slider_container" {...Drag()} style={{...sliderStyle}}>Slide me</a.div>
      <div className="slider_left-side">
        <div className="slider_left-side__circle__wrapper">
          <a.div className="slider_left-side__circle" style={{...leftSliderCircle}}></a.div>
        </div>
      </div>
      <div className="slider_right-side">
        <div className="slider_right-side__circle__wrapper">
          <a.div className="slider_right-side__circle" style={{...rightSliderCircle}}></a.div>
        </div>
      </div>
    </div>
  );
};

export default AnimatedSlide;