import React, { useEffect, useState } from 'react';
import { useSpring, a } from '@react-spring/web';
import { useDrag, useHover } from '@use-gesture/react';
const DragAnimation = () => {
  // const [gestureUpdate, setGestureUpdate] = useState(false)
  document.addEventListener('gesturestart', (e) => e.preventDefault())
  document.addEventListener('gesturechange', (e) => e.preventDefault()) 
  const [{x, y}, api] = useSpring(() => ({x: 0, y: 0, config: {duration: 100}}))
  const bind = useDrag(({ down, movement: [mx, my], cancel}) => {
    console.log(mx, my)
    if (mx > 600 || my > 200) {

      cancel();
      api.start({down, x: 0, y: 0, immediate: down})

    }
    
    api.start({down, x: down ? mx : 0, y: down ? my : 0, immediate: down})
  })
  const [hoverStyle, apiHover] = useSpring(() => ({
      opacity: 1,
      to: {
        opacity: 1,
      }

  }))

  const bindHover = useHover((hover) => {
    console.log(1, hover)
    // hover ? apiHover.start({hover, to: {opacity: 0.5}}) : apiHover.start({hover, opacity: 1});
    
    apiHover.start({ to: {opacity: hover ? 0.5 : 1}})
    console.log(hoverStyle)
    // setGestureUpdate(!gestureUpdate);
  })
  // useEffect(() => {
  //   console.log("effect")

  // }, [gestureUpdate])
  return (
    <>
      <div className="drag-rect__wrapper">
        <a.div className="drag-rect" {...bind()} {...bindHover()} style={{x, y, ...hoverStyle}} onMouseLeave={() => {
          apiHover.start({to: {
            opacity: 1,
          }})}
    }>

        </a.div>
      </div>
    </>

  );
};

export default DragAnimation;