import React, {useEffect, useRef, useState} from 'react';
import { useSpring, animated } from '@react-spring/web';
const AnimatedTimerUpdated = () => {
  const [confirmed, setConfirmed] = useState(false);

  const InputRef = useRef();
  const [style, api] = useSpring(() => ({
    number: 1,
    from: {
      number: 1
    },
    // to: {
    //   number: 3
    // },
    config: {
      duration: 100000
    }
  }))
  const [disappearingMessage, apiMessage] = useSpring(() => ({
    from: {
      width: "100%",
    }
  }))
  function NewUpdate() {
    apiMessage.start({to : {width: "0%"}});
    setTimeout(() => {
      setConfirmed(true);
    }, 600);

    
  }
  function StartTimer() {
    const timerValue = InputRef.current.value;
    // console.log(typeof timerValue)
    api.start({to: {
      number: +timerValue,
    },
    config: {
      duration: +timerValue * 1000,
    }
    })
  }
  useEffect(() => {
  }, [confirmed])
  const [styleForInput, apiForInput] = useSpring(() => ({
    from: {
      width: 20
    },
    to: {
      width: 300
    }
  }))
  console.log(style)
  return (
    <div style={{width: 300, paddingTop: 100, position: "relative"}}>
      {/* {confirmed ? (<animated.div>{style.number}</animated.div>) : (<animated.div style={{...disappearingMessage}}> Type the timer</animated.div>)} */}
      <animated.div style={{overflow: "hidden", whiteSpace: 'nowrap', position: "absolute", top: 50,  ...disappearingMessage}}> Type the timer</animated.div>
      {confirmed ? (<animated.div style={{position: "absolute", top: 50}}>{style.number.to(n => {return n.toFixed(2)})}</animated.div>) : null}
      {/* <animated.div>{style.number}</animated.div> */}
      <button onClick={() => {
        confirmed ? StartTimer() : NewUpdate();
        NewUpdate()} }>{confirmed ? "start there" : "type the timer"}</button>
      {confirmed ? (<input className='input' ref={InputRef} style={{...styleForInput}}></input>) : null}
    </div>
  );
};

export default AnimatedTimerUpdated;