import React, { useEffect, useRef } from 'react';
import { useSpring, animated } from '@react-spring/web';
const AnimatedCircle = () => {
  const number = useRef(0);
  const [style, api] = useSpring(() => ({
    to: {
      strokeDasharray: "628, 0"
    },
    from: {
      strokeDasharray: "0, 628"
    },
    config: {
      duration: 500,
      // mass: 100,
      // tension: 2000,
      friction: 1000,
    }
  }))
  // useEffect(() => {
  //   api.start()
  // }, )
  return (
    <div style={{height: 300, display: 'flex'}}>
      <svg style={{height: 300, display: 'flex'}}>
        <animated.circle style={style} r={100} cx={150} cy={150} stroke="black" fill='none' strokeWidth={10} strokeLinecap={"round"}></animated.circle>
      </svg>
      <button style={{width: 200, height: 40}} onClick={() => {
        api.start({loop: {reverse: true, loop: () => 1 > number.current++}})
        number.current = 0;
      }}>Animate the Circle</button>
    </div>
  );
};

export default AnimatedCircle;