import React, {useState} from 'react';
import { useSpring, animated } from '@react-spring/web';
const AnimatedText = () => {
  const [setting, setSetting] = useState(false)
  const [style, styleAPI] = useSpring(() => ({
    from: {
      height: 0
    },
    to: {
      height: 0
      // height: setting ? 100 : 0
    }
  }))
  return (
    <>
      <animated.div style={
        {
          overflow: "hidden",
          ...style}}>
        <h1>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Dolorum nisi, nesciunt ipsa illum ab aperiam perferendis tenetur obcaecati blanditiis voluptatum veniam sit vel ipsum. Excepturi totam eius neque omnis architecto?</h1>
      </animated.div>

      <button onClick={() => {
        setSetting(!setting);

        setting ? (styleAPI.start({to: {height: 100}})) : (styleAPI.start({to: {height: 0}}))
        // styleAPI.start({to: {height: 100}});

        }
        
        }>Click</button>
    </>


  );
};

export default AnimatedText;