import React, { useEffect, useState } from 'react';
import { useSpring, animated, useSpringRef } from '@react-spring/web';
const AnimatedRectV2 = () => {
  const [styles, api] = useSpring(() => ({
    loop: true,
    from: {
      rotateZ: 0,
      // x: 0,
      // y: 0,
    },
    to: {
      rotateZ: 180
    },
    config: {
      duration: 1000
    }
  }))
  const [move, setMove] = useSpring(() => ({
    from: {
      x: 0,
      y: 0,
    }
  }))


  const springRef = useSpringRef();
  const {size, ...rest} = useSpring({
    // ref: springRef,
    // number: 10,
    from: {size: 1, background: `hotpink`, number: 0},
    to: {
      size: 3,
      background: `white`,
      number: 5,
    },
    config: {
      duration: 2000
    }
  })


  function FollowingByMouse(e) {
    const [x, y] = [e.pageX, e.pageY];
    console.log(x, y)
    return setMove.start({
      x: x - 75,
      y: y - 75,
    })
  }


  const array = [...Array(9).keys()];


  const [confirm, setConfirm] = useState(false);

  const [mainCard, mainCardApi] = useSpring(() => ({
    transform: `translateY(120%) translateX(110%)`,
    from: {
      opacity: 1,
      scale: 1,
      index: 100,
    },
  
  }))

  const [secondaryCard, secondaryCardApi] = useSpring(() => ({
    from: {
      opacity: 0,
      scale: 0,
    }
  }))

  function StartAnimation() {
    console.log(1)
    mainCardApi.start({to: {
      opacity: 0,
      scale: 10,
      index: -1,

    }})
    secondaryCardApi.start({to: {
      opacity: 1,
      scale: 1
      
    }})
  }
  function CancelAnimation() {
    setConfirm(!confirm);
    mainCardApi.start({to: {
      opacity: 1,
      scale: 1,
      index: 100
    }});
    secondaryCardApi.start({to: {
      opacity: 0,
      scale: 0,
    }});
  }
  useEffect(() => {
    // console.log(springRef.current, springRef.current[0].springs.number, springRef.current[0]);
    console.log(mainCard, size)
    
  }, []);

  setTimeout(() => {document.querySelector(".hui").style.display = "none"}, 3000)
  return (
    <>
      <animated.div className='rectangle_wrapper' style={{...move}} onMouseEnter={(e) => {FollowingByMouse(e)}} onMouseMove={(e) => {FollowingByMouse(e)}}>
        <animated.div className="rectangle" style={{...styles}}>
          {/* {} */}
        </animated.div>

      </animated.div>

      <animated.div className="hui" style={{height: 100, transform: size.to((n) => `scale(${n})`) , ...rest}}>
        <animated.div >{rest.number}</animated.div>
      </animated.div>
      <div className="wrapper">
        <animated.div className="cards">
          <animated.div className="cards__main-card" onClick={() => {StartAnimation()}} style={{transform: mainCard.scale.to(n => `scale(${n})`), zIndex: mainCard.index.to(n => n), ...mainCard}}></animated.div>
          {array.map(item => {
            return (<animated.div className="cards_secondary-cards" style={{...secondaryCard}} onClick={() => {CancelAnimation()}}>{item}</animated.div>)
          })}
          
        </animated.div>
      </div>


    </>

  );
};

export default AnimatedRectV2;