import React from 'react';
import AnimatedRect from './AnimatedRect';
import AnimatedRectV2 from './AnimatedRectV2';
import AnimatedText from './AnimatedText';
import AnimatedTimer from './AnimatedTimer';
import AnimatedCircle from './AnimatedCircle';
import AnimatedTimerUpdated from './AnimatedTimerUpdated';
import DragAnimation from './DragAnimation';
import AnimatedSlide from './AnimatedSlide';

const App = () => {
  return (
    <div>
      <AnimatedTimer />
      <AnimatedRect />
      <AnimatedText />
      <AnimatedCircle />
      <AnimatedTimerUpdated />
      <AnimatedRectV2 />
      <DragAnimation />
      <AnimatedSlide />
    </div>
  );
};

export default App;